import { App, Component, createApp } from 'vue'
import { createI18n } from 'vue-i18n'
import type { TV } from '@tv/loader'
import {
    I18n,
    messages as sharedMessages
} from '@tv/shared'
import { messages as connectMessages } from '@tv/connect/src/i18n/messages'
import {
    bbPlugin,
    ecastPlugin,
    fatalPlugin,
    i18nPlugin,
    modalPlugin
} from '@tv/vue'
import { messages } from './i18n/messages'
import MainView from './views/Main.vue'

window.tv.register({
    mount: (mountOptions: TV.MountOptions) => {
        // create the app
        let app: App | null = createApp(MainView as Component)

        // TODO: remove unwrapInjectedRef after Vue 3.3 release
        // https://vuejs.org/guide/components/provide-inject.html#working-with-reactivity
        app.config.unwrapInjectedRef = true

        // i18n

        // Start with the user's device preference by not specifying a locale
        let locale

        // Match the locale of the current room if one exists
        // TODO: Figure out if this is actually beneficial for moderators. Would
        // it be better to always let them use their device-preferred language?
        if (mountOptions.room?.locale) {
            locale = mountOptions.room.locale
        }

        // Override the locale with any specified as a query parameter
        if (mountOptions.match?.params?.locale) {
            locale = mountOptions.match.params.locale
        }

        I18n.set(locale)
        const i18n = createI18n({
            fallbackLocale: 'en',
            locale: I18n.locale,
            messages: I18n.mergeMessages(sharedMessages, messages, connectMessages)
        })

        // plugins
        app.use(bbPlugin)
        app.use(ecastPlugin)
        app.use(fatalPlugin)
        app.use(i18n)
        app.use(i18nPlugin, { i18n })
        app.use(modalPlugin)

        // let's go
        app.mount('#app')

        return () => {
            app!.unmount()
            app = null
        }
    },

    info: (mountOptions: TV.MountOptions) => ({
        tag: '@moderator',
        version: mountOptions.version,
        type: mountOptions.type,
        branch: mountOptions.branch
    })
})
