<template>
<div class="kicking" :class="{ showing: shouldShow }">
    <button class="kick-toggle" :disabled="!players.length" v-on:click="onKickToggleClick">
        <svg viewBox="0 0 23 40" class="tab left">
            <path vector-effect="non-scaling-stroke" d="M23,0A16.41,16.41,0,0,0,7.18,12.08L0,40" />
        </svg>
        <KickSVG class="icon" />
        {{ $t('ACTION.KICK_PLAYERS') }}
        <svg viewBox="0 0 23 40" class="tab right">
            <path vector-effect="non-scaling-stroke" d="M23,0A16.41,16.41,0,0,0,7.18,12.08L0,40" />
        </svg>
    </button>
    <div class="players">
        <div v-for="player in players" :key="player.id" class="player" :class="{ kicked: player.isKicked }">
            <span class="name">{{ player.name }}</span>
            <button
                v-if="!player.isKicked"
                class="kick-button"
                v-on:click="onKickClick(player)"
            >
                {{ $t('ACTION.KICK') }}
            </button>
            <span v-else v-t="'STATUS.KICKED'" class="kick-message" />
        </div>
    </div>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { Analytics } from '@tv/shared'
import KickSVG from '../images/kick-icon.svg.vue'
import type { Moderator } from '../Moderator'

export default defineComponent({
    components: {
        KickSVG
    },

    props: {
        hasStarted: Boolean
    },

    data() {
        return {
            shouldShow: false,
            players: <Moderator.Player[]> []
        }
    },

    beforeMount() {
        this.processWelcomeMessage()
        this.bindConnectedEvent()
        this.bindKickedEvent()
    },

    methods: {
        processWelcomeMessage() {
            Object.values(this.$ecastWelcome.here).forEach((profile) => {
                if (!profile.roles.player) return

                this.players.push({
                    name: profile.roles.player.name,
                    id: profile.id,
                    isKicked: profile.roles.kicked
                })
            })
        },

        bindConnectedEvent() {
            this.$ecast.on('client/connected', (client) => {
                if (client.role !== 'player') return

                const player = this.players.find((player) => player.id === client.id)
                if (player) return

                this.players.push({
                    name: client.name,
                    id: client.id,
                    isKicked: false
                })
            })
        },

        bindKickedEvent() {
            this.$ecast.on('client/kicked', (player) => {
                this.setKicked(player.id, true)
            })
        },

        setKicked(id: number, isKicked: boolean) {
            const player = this.players.find((player) => player.id === id)
            if (!player) return
            player.isKicked = isKicked
        },

        onKickToggleClick() {
            this.shouldShow = !this.shouldShow
        },

        async onKickClick(player: Moderator.Player) {
            player.isKicked = true

            try {
                await this.$ecast.kick(player.id, true)
                Analytics.playerKicked(this.$ecastRoom.appTag, !this.hasStarted)
            } catch (error) {
                player.isKicked = false
                this.$handleEcastError(error)
            }
        }
    }
})
</script>


