<template>
<Fatal v-if="shouldShowFatal" />
<div v-else class="jbg schemable moderator" :class="classes">
    <header class="header">
        <div class="logo" />
    </header>

    <Connect
        v-if="!hasConnected"
        :game="game"
        :room="room"
        v-on:connection-did-change="onConnectionDidChange"
        v-on:room-did-change="onRoomDidChange"
    />
    <Moderate
        v-else
        v-bind="ecastValues"
        :game="game"
        :room="room"
        :is-connected="isConnected"
        :has-started="hasStarted"
    />

    <Modal />
</div>
</template>


<script lang="ts">
import { defineComponent, inject } from 'vue'
import type { GetRoomReply } from '@jackboxgames/ecast'
import { Storage } from '@tv/shared'
import { InjectionKeys } from '@tv/vue'
import { Games, getGame } from '@games/games'
import Connect from './Connect.vue'
import Moderate from './Moderate.vue'
import type { Moderator } from '../Moderator'

export default defineComponent({
    // eslint-disable-next-line vue/component-definition-name-casing
    name: '@moderator',

    components: {
        Connect,
        Moderate
    },

    props: {
        items: {
            default: () => [],
            type: Array as Prop<Moderator.Item[]>
        }
    },

    // Typesafe injections are only available in the composition api
    setup() {
        return {
            fatalError: inject(InjectionKeys.fatal.error)
        }
    },

    data() {
        return {
            isConnected: false,
            hasConnected: false,
            hasStarted: false,
            theme: Storage.get('preference:theme') || 'device',
            room: <GetRoomReply | null> null
        }
    },

    computed: {
        ecastValues(): Record<string, any> | null {
            if (!this.$ecastValues) return null
            return this.$ecastValues
        },

        classes(): string[] {
            const classes = [
                `locale-${this.$i18n.locale}`,
                `theme-${this.theme}`
            ]

            return classes
        },

        game(): Games.Game | null {
            if (!this.room) return null
            return getGame(this.room.appTag) ?? null
        },

        shouldShowFatal(): boolean {
            return this.fatalError?.hasCrashed ?? false
        }
    },

    beforeMount() {
        window.addEventListener('storage', this.onStorageChange)
    },

    beforeUnmount() {
        window.removeEventListener('storage', this.onStorageChange)
    },

    methods: {
        onStorageChange(event: StorageEvent) {
            if (event.key !== 'tv:preference:theme') return
            this.theme = Storage.get('preference:theme') || 'device'
        },

        onConnectionDidChange(isConnected: boolean) {
            if (!this.hasConnected && isConnected) {
                this.hasConnected = true
                this.$ecast.on('room/exit', () => { void this.onRoomExit() })
                this.$ecast.on('socketClose', () => { void this.onSocketClose() })
                this.$ecast.on('room/lock', () => { void this.onRoomLock() })
                this.hasStarted = this.$ecastRoom.locked
            }

            this.isConnected = isConnected
        },

        onRoomDidChange(room: GetRoomReply | null) {
            this.room = room
        },

        async onRoomExit() {
            Storage.remove('moderator-code')

            await this.$showModal('Error', {
                text: this.$t('ERROR.ROOM_DISCONNECTED'),
                subtext: this.$t('ERROR.ROOM_DESTROYED'),
                dismissText: this.$t('ACTION.OK')
            })

            window.location.reload()
        },

        async onSocketClose() {
            Storage.remove('moderator-code')

            await this.$showModal('Error', {
                text: this.$t('ERROR.DISCONNECTED'),
                dismissText: this.$t('ACTION.OK')
            })

            window.location.reload()
        },

        onRoomLock() {
            this.hasStarted = true
        }
    }
})
</script>

<style lang="scss" scoped>
@use "@tv/shared/src/styles/jbg.scss" as jbg;

.jbg.moderator {
    height: 100%;
    overflow-y: scroll;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    width: 100%;
    padding: 12px 15px;
    background: jbg.$blueDarker;
    z-index: 100;

    .logo {
        width: 90%;
        height: 28px;
        margin: 0 auto;
        background-image: url("../images/header-logo.svg");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;

        .locale-fr & {
            background-image: url("../images/header-logo.fr.svg");
        }

        .locale-it & {
            background-image: url("../images/header-logo.it.svg");
        }

        .locale-es & {
            background-image: url("../images/header-logo.es.svg");
        }
    }
}
</style>
