<template>
<div class="constrain">
    <form autocomplete="off" v-on:submit.prevent="onFormSubmit">
        <fieldset>
            <label for="roomcode">
                {{ $t('FORM.ROOM_CODE') }}
                <span v-if="state.statusKey" v-t="state.statusKey" class="status" />
                <span v-else-if="state.statusText" class="status">{{ state.statusText }}</span>
            </label>
            <TextInput
                id="roomcode"
                v-model="code"
                type="text"
                autocapitalize="off"
                autocorrect="off"
                autocomplete="off"
                :placeholder="$t('FORM.ROOM_CODE_PLACEHOLDER')"
                :maxlength="codeLength"
                v-on:input="onCodeInput"
            />

            <label v-t="'FORM.PASSWORD'" for="password" />
            <TextInput
                id="password"
                v-model="password"
                type="text"
                inputmode="numeric"
                autocapitalize="off"
                autocorrect="off"
                autocomplete="off"
                :placeholder="$t('FORM.PASSWORD_PLACEHOLDER')"
                :maxlength="passwordLength"
                v-on:input="onPasswordInput"
            />

            <button
                type="submit"
                :class="{ connecting: isConnecting }"
                :disabled="!state.canSubmit"
            >
                <span v-t="'ACTION.MODERATE'" />
                <div class="loading" />
            </button>
        </fieldset>
    </form>

    <p v-if="room && !state.isSupported" class="warning">
        {{ $t('WARNING.UNSUPPORTED', { gameName: game?.name ?? 'This game' }) }}
    </p>
    <p v-else-if="room && !state.isEnabled" class="warning">
        {{ $t('WARNING.DISABLED') }}
    </p>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { APIClient, WSClient } from '@jackboxgames/ecast'
import type { GetRoomReply } from '@jackboxgames/ecast'
import { Analytics, Storage, Utils } from '@tv/shared'
import type { Games } from '@games/games'
import { TextInput } from '@tv/vue'

interface State {
    canSubmit: boolean
    isEnabled: boolean
    isSupported: boolean
    statusText?: string
    statusKey?: string
}

export default defineComponent({
    components: {
        TextInput
    },

    props: {
        game: Object as Prop<Games.Game | null>,
        room: Object as Prop<GetRoomReply | null>
    },

    emits: {
        connectionDidChange: (_: boolean) => true,
        roomDidChange: (_: GetRoomReply | null) => true
    },

    data() {
        return {
            codeLength: 4,
            code: '',
            passwordLength: 5,
            password: '',
            isConnecting: false,
            api: new APIClient({
                host: Utils.serverUrl,
                scheme: 'https'
            }),
            roomNotFound: false
        }
    },

    computed: {
        hasFormData(): boolean {
            if (this.code.length !== this.codeLength) return false
            if (this.password.length !== this.passwordLength) return false
            return true
        },

        state(): State {
            if (!this.room) {
                const showNotFound = this.code.length === this.codeLength && this.roomNotFound

                return {
                    canSubmit: false,
                    isEnabled: false,
                    isSupported: false,
                    statusKey: showNotFound ? 'STATUS.ROOM_NOT_FOUND' : ''
                }
            }

            const features = this.game?.features ?? []
            const isSupported = features.includes('moderation') || features.includes('kicking')
            const isEnabled = this.room.moderationEnabled

            if (!isSupported) {
                return {
                    canSubmit: false,
                    isEnabled,
                    isSupported,
                    statusKey: 'STATUS.UNSUPPORTED'
                }
            }

            if (!isEnabled) {
                return {
                    canSubmit: false,
                    isEnabled,
                    isSupported,
                    statusKey: 'STATUS.DISABLED'
                }
            }

            return {
                canSubmit: this.hasFormData,
                isEnabled,
                isSupported,
                statusText: this.game?.name ?? '???'
            }
        }
    },

    beforeMount() {
        void this.populateFromStorage()
    },

    methods: {
        async populateFromStorage() {
            if (!Storage.isSupported) return

            this.code = (Storage.get('moderator-code') ?? '').toUpperCase()
            this.password = (Storage.get('moderator-password') ?? '').toUpperCase()

            if (this.code) {
                await this.getRoomInfo()
            }

            if (!this.room) {
                this.code = ''
                this.password = ''
                Storage.remove('moderator-code')
                Storage.remove('moderator-password')
            }
        },


        // Form

        onCodeInput() {
            // Room codes shouldn't have spaces, replace them all
            const regexSpace = /\s/g
            this.code = this.code
                .replace(regexSpace, '')
                .trim()
                .toUpperCase()

            if (this.code.length !== this.codeLength) {
                this.$emit('roomDidChange', null)
                return
            }

            void this.getRoomInfo()
        },

        onPasswordInput() {
            // Passwords shouldn't have spaces, replace them all
            const regexSpace = /\s/g
            this.password = this.password
                .replace(regexSpace, '')
                .trim()
        },

        async onFormSubmit() {
            await this.connect()
        },


        // Room

        async getRoomInfo() {
            this.roomNotFound = false

            try {
                const room = await this.api.getRoom({ code: this.code })
                this.$emit('roomDidChange', room)

                // Convert any text to the locale of the room,
                // which may differ from the user's preference.
                // TODO: When this.room is cleared, reset locale to user's preferred
                this.$i18n.locale = room.locale
            } catch (error) {
                console.warn(error)
                this.$emit('roomDidChange', null)
                this.roomNotFound = true
            }
        },


        // Connect

        async connect() {
            if (this.isConnecting) return
            if (!this.state.canSubmit) return

            await this.getRoomInfo()

            if (!this.room) {
                return this.showError('ERROR.ROOM_NOT_FOUND')
            }

            const options: WSClient.InitOptions = {
                host: this.room.host,
                code: this.code.toUpperCase(),
                name: 'moderator',
                password: this.password,
                role: 'moderator',
                debug: import.meta.env.TV_DEBUG
            }

            const client = new WSClient(options)

            try {
                this.isConnecting = true
                const welcome = await client.connect()

                this.$setupEcast({
                    room: this.room,
                    wsClient: client,
                    welcome
                })

                Storage.set('moderator-code', this.code)
                Storage.set('moderator-password', this.password)

                Analytics.moderatorConnected(this.room.appTag)

                this.$emit('connectionDidChange', true)
            } catch (error) {
                console.error('[moderator]', error)
                this.onConnectionError()
            }

            this.isConnecting = false
        },


        // Errors

        onConnectionError() {
            this.showError('ERROR.GENERIC')
        },

        showError(subtextKey: string) {
            void this.$showModal('Error', {
                text: this.$t('ERROR.TITLE'),
                subtext: this.$t(subtextKey),
                dismissText: this.$t('ACTION.OK')
            })
        }
    }
})
</script>


<style lang="scss" scoped>
@use "@tv/shared/src/styles/jbg.scss" as jbg;

label {
    .status {
        float: right;
        font-size: 14px;
        font-weight: normal;
        font-style: italic;
        color: var(--textSecondary);
    }
}

.warning {
    padding: 0 30px;
    font-size: 15px;
    font-weight: 400;
    font-style: italic;
    text-align: center;
    color: var(--alert);
}
</style>
