<template>
<div ref="stage" class="stage" />
<p class="name">{{ $t('SUBMITTED_BY') }} <span>{{ item!.name }}</span></p>
<div class="item-buttons">
    <button
        aria-label="accept"
        class="accept"
        :disabled="item!.status !== 'pending'"
        v-on:click="$emit('acceptWasClicked', item!)"
    >
        <ApproveSVG class="icon" />
    </button>
    <button
        aria-label="reject"
        class="reject"
        :disabled="item!.status === 'rejected'"
        v-on:click="$emit('rejectWasClicked', item!)"
    >
        <RejectSVG class="icon" />
    </button>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { DrawStage } from '@tv/shared'
import ApproveSVG from '../../images/approve-icon.svg.vue'
import RejectSVG from '../../images/reject-icon.svg.vue'
import type { Moderator } from '../../Moderator'

export default defineComponent({
    components: {
        ApproveSVG,
        RejectSVG
    },

    props: {
        item: Object as Prop<Moderator.AnimationItem>
    },

    emits: {
        acceptWasClicked: (_: Moderator.AnimationItem) => true,
        rejectWasClicked: (_: Moderator.AnimationItem) => true
    },

    mounted() {
        const stageEl = this.$refs.stage as HTMLElement
        const width = this.item!.value.size.width
        const frames = this.item!.value.frames || []
        const stage = new DrawStage(stageEl, {
            width: this.item!.value.size.width * frames.length,
            height: this.item!.value.size.height
        })
        const context = stage.canvas.renderCanvas.getContext('2d')
        if (!context) return

        frames.forEach((frame, index) => {
            context.save()
            context.translate(width * index, 0)
            const lines = stage.canvas.parseLines(frame)
            lines.forEach((line) => stage.canvas.drawLine(context, line))
            context.restore()
        })
    }
})
</script>


<style lang="scss" scoped>
@use "@tv/shared/src/styles/jbg.scss" as jbg;
@use "../../styles/vars.scss" as moderator;

.stage {
    width: 100%;
    max-width: 300px;
    margin-top: 15px;
    border: 2px solid var(--textSecondary);
    border-radius: 5px;
    background: #FFF;
    pointer-events: none;

    .accepted &,
    .rejected & {
        opacity: 0.5;
        border-color: var(--inputPrimary);
    }
}
</style>
