<template>
<div class="constrain">
    <div class="top">
        <p><span>{{ $t('FORM.GAME') }}:</span>{{ game?.name ?? '???' }}</p>
    </div>

    <Kicking
        v-if="hasKicking"
        :class="{ expand: !hasModeration }"
        :has-started="hasStarted"
    />

    <template v-if="hasModeration">
        <div class="alls">
            <button class="all accept" :disabled="!pendingItems.length" v-on:click="onAcceptAllClick">
                <ApproveSVG class="icon" />
                {{ $t('ACTION.APPROVE_ALL') }}
            </button>
            <button class="all reject" :disabled="!pendingItems.length" v-on:click="onRejectAllClick">
                <RejectSVG class="icon" />
                {{ $t('ACTION.REJECT_ALL') }}
            </button>
        </div>

        <div v-if="!items.length" class="empty">
            <p v-t="'STATUS.WAITING_FOR_SUBMISSIONS'" class="empty-text" />
            <div class="loading disabled" />
        </div>
        <transition-group v-else name="items" tag="div" class="items">
            <template v-for="item in items" :key="item.key">
                <div class="item text" :class="item.status">
                    <p v-if="item.context" v-bb="htmlUnescape(item.context)" class="context" />
                    <AnimationItem
                        v-if="item.type === 'animation'"
                        :item="item"
                        v-on:accept-was-clicked="onAcceptClick"
                        v-on:reject-was-clicked="onRejectClick"
                    />
                    <DoodleItem
                        v-else-if="item.type === 'doodle'"
                        :item="item"
                        v-on:accept-was-clicked="onAcceptClick"
                        v-on:reject-was-clicked="onRejectClick"
                    />
                    <DrawingItem
                        v-else-if="item.type === 'drawing'"
                        :item="item"
                        v-on:accept-was-clicked="onAcceptClick"
                        v-on:reject-was-clicked="onRejectClick"
                    />
                    <TextItem
                        v-else-if="item.type === 'text'"
                        :item="item"
                        v-on:accept-was-clicked="onAcceptClick"
                        v-on:reject-was-clicked="onRejectClick"
                    />
                </div>
            </template>
        </transition-group>
    </template>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import type { GetRoomReply } from '@jackboxgames/ecast'
import { Analytics, Utils } from '@tv/shared'
import type { Games } from '@games/games'
import Kicking from './Kicking.vue'
import AnimationItem from './items/AnimationItem.vue'
import DoodleItem from './items/DoodleItem.vue'
import DrawingItem from './items/DrawingItem.vue'
import TextItem from './items/TextItem.vue'
import ApproveSVG from '../images/approve-icon.svg.vue'
import RejectSVG from '../images/reject-icon.svg.vue'
import type { Moderator } from '../Moderator'

export default defineComponent({
    components: {
        Kicking,
        AnimationItem,
        DoodleItem,
        DrawingItem,
        TextItem,
        ApproveSVG,
        RejectSVG
    },

    props: {
        items: {
            default() { return [] },
            type: Array as Prop<Moderator.Item[]>
        },
        isConnected: Boolean,
        hasStarted: Boolean,
        game: Object as Prop<Games.Game | null>,
        room: Object as Prop<GetRoomReply | null>
    },

    ecastProviders: {
        items: {
            hasDeepRefs: true,
            fn: (values) => {
                const items: Moderator.Item[] = []

                Object.keys(values).forEach((key) => {
                    const splitKey = key.split(':')
                    if (splitKey[0] !== 'moderate') return

                    const type = splitKey[1] as Moderator.Type
                    if (!(['animation', 'doodle', 'drawing', 'text'].includes(type))) return

                    const item: Moderator.Item = {
                        key,
                        type,
                        status: values[key].status,
                        name: values[key].name,
                        value: values[key].value,
                        context: ''
                    }

                    if (values[key].context) {
                        if (values[key].context.bb) {
                            item.context = values[key].context.bb
                        } else if (values[key].context.html) {
                            item.context = Utils.htmlTagsToBBCode(values[key].context.html as string, [['i', 'i']])
                        } else if (values[key].context.text) {
                            item.context = values[key].context.text
                        } else if (typeof values[key].context === 'string') {
                            item.context = values[key].context
                        }
                    }

                    items.push(item)
                })

                return items
            }
        }
    },

    data() {
        return {}
    },

    computed: {
        hasKicking() {
            if (!this.game?.features) return false
            return this.game.features.includes('kicking')
        },

        hasModeration() {
            if (!this.game?.features) return true
            return this.game.features.includes('moderation')
        },

        pendingItems(): Moderator.Item[] {
            return this.items.filter((item) => item.status === 'pending')
        }
    },

    methods: {
        htmlUnescape(value: string): string {
            return Utils.htmlUnescape(value)
        },

        onAcceptAllClick() {
            void this.updateStatus(this.pendingItems, 'accepted')
        },

        onRejectAllClick() {
            void this.updateStatus(this.pendingItems, 'rejected')
        },

        onAcceptClick(item: Moderator.Item) {
            if (item.status !== 'pending') return
            void this.updateStatus([item], 'accepted')
        },

        onRejectClick(item: Moderator.Item) {
            if (item.status === 'rejected') return
            void this.updateStatus([item], 'rejected')
        },

        async updateStatus(items: Moderator.Item[], status: Moderator.Status) {
            try {
                const updates = items.map((item) => {
                    const val = this.$ecast.entities[item.key].val
                    val.status = status
                    return this.$ecast.updateObject(item.key, val)
                })
                await Promise.all(updates)
            } catch (err) {
                console.error('[Moderation] unable to update moderation entities', err)
            }

            // Perplexingly, PP7 games create moderation entities, but don't
            // respond to update notifications on those entities. The controller
            // must update the host using `client/send`.
            // TODO: Make this less noisy for games whose hosts use host reconnect.
            try {
                await this.$ecast.mail(1, {
                    id: items.map((item) => item.key),
                    status
                })
            } catch (err) {
                console.error('[Moderation] unable to notify host by mail', err)
            }

            items.forEach(() => {
                Analytics.itemModerated(this.room!.appTag, status === 'rejected')
            })

            this.$syncEcast()
        }
    }
})
</script>


