<template>
<div class="stage" :style="{ background: item!.value.background }">
    <img :src="itemSrc" alt="">
</div>
<p class="name">{{ $t('SUBMITTED_BY') }} <span>{{ item!.name }}</span></p>
<div class="item-buttons">
    <button
        aria-label="accept"
        class="accept"
        :disabled="item!.status !== 'pending'"
        v-on:click="$emit('acceptWasClicked', item!)"
    >
        <ApproveSVG class="icon" />
    </button>
    <button
        aria-label="reject"
        class="reject"
        :disabled="item!.status === 'rejected'"
        v-on:click="$emit('rejectWasClicked', item!)"
    >
        <RejectSVG class="icon" />
    </button>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { DoodleCanvas } from '@tv/shared'
import ApproveSVG from '../../images/approve-icon.svg.vue'
import RejectSVG from '../../images/reject-icon.svg.vue'
import type { Moderator } from '../../Moderator'

export default defineComponent({
    components: {
        ApproveSVG,
        RejectSVG
    },

    props: {
        item: Object as Prop<Moderator.DoodleItem>
    },

    emits: {
        acceptWasClicked: (_: Moderator.DoodleItem) => true,
        rejectWasClicked: (_: Moderator.DoodleItem) => true
    },

    data() {
        return {
            canvas: null as DoodleCanvas | null
        }
    },

    computed: {
        itemSrc(): string {
            if (!this.canvas) return ''

            return this.canvas.renderImage()
        }
    },

    mounted() {
        this.canvas = new DoodleCanvas(
            document.createElement('canvas'),
            this.item!.value.doodle
        )
    }
})
</script>


<style lang="scss" scoped>
@use "@tv/shared/src/styles/jbg.scss" as jbg;
@use "../../styles/vars.scss" as moderator;

.stage {
    width: 100%;
    max-width: 300px;
    margin-top: 15px;
    border: 1px solid jbg.$offWhite;
    border-radius: 5px;
    background: #fff;

    // "transparent background" checkerboard pattern
    // overridden if moderation value includes `background`
    background-image: linear-gradient(45deg, #aaaaaa 25%, transparent 25%),
        linear-gradient(-45deg, #aaaaaa 25%, transparent 25%),
        linear-gradient(45deg, transparent 75%, #aaaaaa 75%),
        linear-gradient(-45deg, transparent 75%, #aaaaaa 75%);
    background-position: 0 0, 0 5px, 5px -5px, -5px 0px;
    background-size: 10px 10px;

    .accepted & {
        border-color: moderator.$approve;
    }

    .rejected & {
        border-color: moderator.$reject;
    }
}
</style>
