<template>
<div ref="stage" class="stage" />
<p class="name">{{ $t('SUBMITTED_BY') }} <span>{{ item!.name }}</span></p>
<div class="item-buttons">
    <button
        aria-label="accept"
        class="accept"
        :disabled="item!.status !== 'pending'"
        v-on:click="$emit('acceptWasClicked', item!)"
    >
        <ApproveSVG class="icon" />
    </button>
    <button
        aria-label="reject"
        class="reject"
        :disabled="item!.status === 'rejected'"
        v-on:click="$emit('rejectWasClicked', item!)"
    >
        <RejectSVG class="icon" />
    </button>
</div>
</template>


<script lang="ts">
import { defineComponent } from 'vue'
import { DrawStage } from '@tv/shared'
import ApproveSVG from '../../images/approve-icon.svg.vue'
import RejectSVG from '../../images/reject-icon.svg.vue'
import type { Moderator } from '../../Moderator'

export default defineComponent({
    components: {
        ApproveSVG,
        RejectSVG
    },

    props: {
        item: Object as Prop<Moderator.DrawingItem>
    },

    emits: {
        acceptWasClicked: (_: Moderator.DrawingItem) => true,
        rejectWasClicked: (_: Moderator.DrawingItem) => true
    },

    mounted() {
        const stageEl = this.$refs.stage as HTMLElement
        const stage = new DrawStage(stageEl, {
            width: this.item!.value.size.width,
            height: this.item!.value.size.height
        })

        const lines = stage.canvas.parseLines(this.item!.value.lines)
        const context = stage.canvas.renderCanvas.getContext('2d')
        if (!context) return
        lines.forEach((line) => stage.canvas.drawLine(context, line))
    }
})
</script>


<style lang="scss" scoped>
@use "@tv/shared/src/styles/jbg.scss" as jbg;
@use "../../styles/vars.scss" as moderator;

.stage {
    width: 100%;
    max-width: 300px;
    margin-top: 15px;
    border: 2px solid jbg.$offWhite;
    border-radius: 5px;
    background: #FFF;
    pointer-events: none;

    .accepted & {
        opacity: 0.5;
        border-color: moderator.$approve;
    }

    .rejected & {
        opacity: 0.5;
        border-color: moderator.$reject;
    }
}
</style>
